
.block {
	display: flex; }

// HEADER
.header {
	background-color: rgba(0, 0, 0, 0.36);
	text-align: center;
	padding: 10px 0;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 200;
	.logo {
		display: inline-block;
		width: 136px;
		margin: 0 auto; }
	a {
		color: white;
		font-size: 14px;
		text-decoration: none;
		text-transform: uppercase; }
	@include rmin(800) {
		position: fixed;
		height: 94px;
		padding: 0;
		.header-aside {
			float: right; }
		.logo {
			float: left;
			line-height: 94px;
			height: 94px;
			position: relative;
			img {
				position: absolute;
				margin: auto;
				top: 0;
				left: 0;
				bottom: 0;
				right: 0; } } } }
.nav-main {
	display: none;
	a {
		display: inline-block;
		padding: 5px;
		transition: opacity 0.2s;
		@include futuris(); }
	@include rmin(800) {
		display: inline-block !important;
		height: 94px;
		line-height: 94px;
		a {
			padding: 0 9px;
			&.js-transparent {
				opacity: 0.4; } } } }
.toggle-lang {
	margin-top: 5px;
	@include futuris();
	@include rmin(800) {
		display: inline-block;
		margin-top: 0; }
	a {
		margin: 0 2px;
		@include rmin(800) {
			&:last-child {
				margin-right: 0; } }
		&.active {
			color: $blue;
			pointer-events: none; } } }

.nav-trigger-wrap {
	display: block;
	margin: 10px 0;
	padding: 2px 0;
	@include rmin(800) {
		display: none; } }

.nav-trigger {
	margin: 0 auto;
	@include burger(30px, 4px, 6px, white);
	&.js-active {
		@include burger-to-cross(); } }
// PROMO
.promo {
	//background-color: #232323
	color: white;
	padding-top: 140px;
	padding-bottom: 30px;
	text-align: center;
	h1 {
		text-transform: uppercase;
		font-size: 24px;
		line-height: 1.4;
		@include rmin(800) {
			font-size: 40px;
			width: 300px;
			margin-bottom: 60px; }
		@include futuris();
		span {
			color: $blue; } }
	p {
		line-height: 1.4;
		max-width: 400px;
		margin: 0 auto;
		@include rmin(800) {
			font-size: 18px;
			line-height: 1.5;
			width: 284px;
			margin: 0; } }
	.woman-img {
		display: none; }
	@include rmin(800) {
		height: 651px;
		text-align: left;
		overflow: hidden;
		padding-top: 0;
		&-left {
			float: left;
			padding-top: 150px; }
		&-right {
			float: right;
			position: relative;
			.gcs-slogan {
				position: absolute;
				right: 40px;
				top: 230px; } }
		.woman-img {
			display: block; } }
	@include rmin(1030) {
		&-right {
			.gcs-slogan {
				right: -40px; } } } }


// welcome

.welcome {
	&-top {
		color: white;
		background-color: #00b3be;
		h2 {
			margin: 0;
			text-align: center;
			font-weight: 400;
			text-transform: uppercase;
			padding: 25px 0;
			max-width: 290px;
			margin: 0 auto;
			@include rmin(800) {
				max-width: 100%; }
			strong {
				font-weight: 700; } } }
	&-block {
		max-width: 400px;
		margin: 40px auto;
		p {
			color: #8e9191;
			line-height: 1.4; }
		@include rmin(800) {
			max-width: 380px;
			float: left;
			&:last-child {
				float: right; } }
		h3 {
			font-weight: 400;
			text-transform: uppercase;
			font-size: 26px;
			margin: 0;
			padding-top: 4px;
			span {
				color: $blue;
				display: block; } }
		&-top {
			position: relative;
			padding-left: 100px;
			height: 80px;
			img {
				position: absolute;
				left: 0; } } } }


// projects
.projects {
	text-align: center;
	h3 {
		font-size: 26px;
		text-transform: uppercase;
		font-weight: 400;
		span {
			color: $blue-dark; } }
	&-filter {
		text-align: center;
		margin-bottom: 30px;
		&-list {
			padding: 0;
			margin: 0;
			list-style: none;
			li {
				display: inline-block;
				margin: 4px 2px; }
			a {
				display: inline-block;
				text-decoration: none;
				padding: 6px 10px;
				border-radius: 4px;
				background-color: #eaedf0;
				transition: all 0.3s;
				&.js-active, &:hover {
					background-color: #00b3be;
					color: white; } } } } }

.projects-gallery {
	@include clr(); }

.projects-item {
	width: 100%;
	float: left;
	position: relative;
	overflow: hidden;
	height: 0;
	padding-bottom: 66%;
	display: block;
	&:hover {
		.overlay {
			opacity: 1; } }
	@include rmin(800) {
		width: 25%;
		padding-bottom: 17%; }
	@include rmin(1100) {
		width: 20%;
		padding-bottom: 13.5%; }
	img {
		display: block;
		width: 100%;
		position: absolute;
		left: 0;
		top: 0; }
	.overlay {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(#394a58, 0.78);
		opacity: 0;
		transition: all 0.3s;
		color: white;
		display: table;
		&-body {
			display: table-cell;
			vertical-align: middle; }
		h4 {
			font-weight: 400;
			font-size: 18px;
			margin: 0; }
		p {
			font-size: 13px;
			margin: 0; }
		span {
			border: 1px solid #ffffff;
			background-color: rgba(255, 255, 255, 0);
			display: inline-block;
			padding: 4px 16px 5px;
			border-radius: 20px;
			margin-top: 10px; } } }

// features
.features {
	text-align: center;
	&-group {
		padding: 20px 0;
		@include rmin(800) {
			margin: 0 -20px; } }
	h2 {
		background-color: #394a58;
		margin: 0;
		color: white;
		text-align: center;
		font-weight: 400;
		text-transform: uppercase;
		padding: 10px 0;
		font-size: 26px;
		@include rmin(800) {
			padding: 20px 0; }
		span {
			color: $blue-dark; } } }

.feature {
	display: inline-block;
	vertical-align: top;
	margin: 20px 0;
	width: 50%;
	margin-right: -3px;
	@include rmin(800) {
		width: auto;
		margin: 20px 35px; }
	&-count {
		color: #394a58;
		font-size: 30px;
		margin: 0;
		@include rmin(800) {
			font-size: 50px; } }
	p {
		font-size: 15px; } }


// mice department
.mice-department {
	padding: 20px 0;
	text-align: center;
	@include rmin(800) {
		padding-bottom: 50px; }
	.globe-big {
		display: none;
		@include rmin(800) {
			display: inline-block;
			width: 50%;
			margin: 0 auto; }
		@include rmin(1000) {
			width: auto;
			max-width: 100%; } }
	h2 {
		font-weight: 400;
		text-transform: uppercase;
		span {
			color: $blue-dark; } } }

.mice-group-left {
	@include rmin(800) {
		float: left;
		p {
			text-align: left; } } }
.mice-group-right {
	@include rmin(800) {
		float: right;
		padding-top: 20px;
		.mice-block:first-child {
			margin-bottom: 50px; }
		p {
			text-align: right; } } }
.mice-block {
	margin: 40px 0;
	.icon {
		height: 75px;
		position: relative;
		img {
			position: absolute;
			margin: auto;
			top: 0;
			bottom: 0;
			right: 0;
			left: 0; } }
	@include rmin(800) {
		width: 180px; }
	h3 {
		text-transform: uppercase;
		font-weight: 400; }
	p {
		@include rmin(800) {
			color: #8e9191; } } }

// event department
.event-department {
	padding: 20px 0;
	text-align: center;
	color: white;
	&-img {
		display: none; }
	@include rmin(800) {
		padding-bottom: 40px;
		.event-advantages {
			height: 407px;
			position: relative; }
		&-img {
			display: block;
			width: 972px;
			position: absolute;
			top: 0;
			left: 50%;
			max-width: none;
			margin: 0 auto;
			margin-left: -486px; } }
	h2 {
		font-weight: 400;
		text-transform: uppercase;
		color: white;
		span {
			color: $blue-dark; } } }

.event-advantages-group {
	@include rmin(800) {
		width: 730px;
		margin: 0 auto;
		position: relative;
		padding-top: 70px;
		@include clr(); } }

.event-advantage {
	margin: 40px 0;
	img {
		display: block;
		margin: 0 auto; }
	&.second {
		h3:before {
			content: '>';
			font-weight: 700;
			left: -28px;
			top: 16px;
			font-size: 26px;
			position: absolute; } }
	h3 {
		font-size: 50px;
		margin: 0;
		margin-top: 10px;
		position: relative;
		display: inline-block; }
	p {
		margin-top: 4px; }
	@include rmin(800) {
		width: 200px;
		h3 {
			font-size: 50px;
			display: inline-block; }
		p {
			line-height: 1.2;
			margin-top: 0;
			font-size: 17px; }
		&.first {
			float: left; }
		&.second {
			float: right; } } }

.events {
	@include clr();
	@include rmin(800) {
		padding-top: 40px;
		margin: 0 -40px; } }

.single-event {
	width: 50%;
	float: left;
	margin: 10px 0;
	text-transform: uppercase;
	.icon {
		position: relative;
		height: 70px;
		img {
			position: absolute;
			margin: auto;
			right: 0;
			left: 0;
			top: 0;
			bottom: 0; } }
	&:nth-child(2n+1) {
		clear: both; }
	@include rmin(1000) {
		width: 20%;
		&:nth-child(2n+1) {
			clear: none; } } }

.news {
	padding: 30px 0;
	.container > p {
		max-width 660px {}
		margin: 0 auto; }
	.row {
		margin-left: -8px;
		margin-right: -8px; }
	h2 {
		font-weight: 400;
		color: $blue-dark;
		font-size: 26px;
		text-align: center;
		text-transform: uppercase; }
	p {
		color: #8e9191;
		font-size: 14px;
		text-align: center;
		@include rmin(800) {
			font-size: 16px; } }
	.news-item p {
		text-align: left; } }

.news-item {
	margin: 40px 0;
	padding: 0 8px;
	text-align: left;
	.btn {
		margin-top: 20px; }
	@include rmin(800) {
		width: 33.3333%;
		float: left;
		&:nth-child(3n+1) {
			clear: left; } } }

.clients {
	text-align: center;
	padding: 30px 0;
	@include rmin(800) {
		padding: 80px 0 100px; }
	h2 {
		color: $blue-dark;
		text-transform: uppercase;
		font-size: 26px;
		font-weight: 400;
		text-transform: uppercase;
		margin-top: 0; } }

.contact {
	padding: 20px 0;
	text-align: center;
	color: #fff;
	@include rmin(800) {
		padding: 70px 0; }
	@include clr();
	a {
		color: white; }
	.links {
		margin-top: 14px; }
	.links a {
		transition: all 0.3s;
		color: #929da8;
		display: inline-block;
		padding: 8px;
		text-decoration: none;
		&:hover {
			color: #fff; } }
	&-left {
		@include rmin(800) {
			float: left;
			text-align: left;
			width: 340px; } }
	&-block {
		margin: 10px 0; }
	&-right {
		@include futuris();
		max-width: 250px;
		margin: 20px auto;
		h3 {
			text-transform: uppercase;
			font-weight: 400; }
		@include rmin(800) {
			float: right;
			font-size: 20px;
			max-width: 400px;
			text-align: left;
			h3 {
				margin-top: 6px; } }
		@include rmin(1000) {
			max-width: 500px; } }
	&-block-phone {
		display: block;
		span {
			display: inline-block;
			min-width: 40px; } } }
address {
	font-style: normal; }


.socials {
	a {
		transition: all 0.3s;
		&:hover {
			opacity: 0.7; } } }

#map {
	height: 300px;
	@include rmin(800) {
		height: 552px; } }
